import { css, polished } from 'jimu-core';
export function itemSelectorStyles(props) {
    const theme = props.theme;
    return css `
    .component-item-selector{
      .item-selector-search{
        .search-input::-ms-clear{
          display: none;
        }
      }

      &.all-mode{
        .item-list-container{
          height: calc(100% - 55px);
        }

        .category-container{
          height: calc(100% - 60px);
        }

        .item-selector-search{
          position: relative;
          margin-left: ${polished.rem(50)};
        }

        .search-input{
          min-width: ${polished.rem(120)};
          input {
            height: 32px;
            line-height: 32px;
          }
        }

        @media only screen and (max-width: 1490px){
          &,.search-input{
            width: 100%;
          }
        }

        @media only screen and (min-width: 1490px){
          &{
            width: auto;
          }
          .search-input{
            width: ${polished.rem(400)};
          }
        }

        @media only screen and (max-width: 1000px){
          margin-left: ${polished.rem(20)};
        }
      }

      &.simple-mode{
        .item-list-container{
          height: 100%;
        }
        .category-container{
          height: calc(100% - 116px);
        }
        .category-container-with-group-select {
          height: calc(100% - 144px);
        }
        .category-container-without-select{
          height: 100%;
          overflow: hidden;
        }
        .item-selector-search-container {
          width: 100%;
          margin-top: ${polished.rem(8)};
          margin-bottom: ${polished.rem(16)};
        }
        .item-selector-search {
          overflow: hidden;
          .search-input {
            font-size: ${polished.rem(13)};
            input {
              min-width: 0;
            }
          }
        }
      }

      .category-nav{
        .nav-item{
          .nav-link{
            .jimu-nav-link-wrapper{
              font-size: ${polished.rem(16)};
            }
          }
        }
        .nav-item.active-tab{
          .nav-link{
            border-bottom: 3px solid ${theme.sys.color.primary.light};
            padding: 0;
            .jimu-nav-link-wrapper{
              color: ${theme.ref.palette.black};
              font-weight: 500;
            }
          }
        }
      }

      .url-input-hint-label{
        font-size: ${polished.rem(12)};
        color: ${theme.ref.palette.neutral[900]};
      }

      .font-size-12{
        font-size: ${polished.rem(12)};
      }

      .mt-30{
        margin-top: ${polished.rem(30)};
      }

      .mb-20{
        margin-bottom: ${polished.rem(20)};
      }

      .nav.nav-underline{
        border: 0;
        .nav-item:not(:last-child){
          @media only screen and (max-width: 1100px){
            &{
              margin-right: ${polished.rem(10)};
            }
          }
          @media only screen and (min-width: 1100px){
            &{
              margin-right: ${polished.rem(25)};
            }
          }
          @media only screen and (min-width: 1300px){
            &{
              margin-right: ${polished.rem(40)};
            }
          }
        }
      }

      .item-selector-sort{
        margin-right: -${polished.rem(16)};
        .item-selector-sort-by{
          color: ${theme.ref.palette.neutral[900]};
          font-size: ${polished.rem(13)};
          height: ${polished.rem(32)};
          .item-selector-sort-select{
            width: ${polished.rem(140)};
            padding: 2px 6px;
            font-size: ${polished.rem(13)};
            height: ${polished.rem(26)};
          }
        }
      }
    }

  `;
}
