import { css, polished } from 'jimu-core';
export function itemDetailStyles(props) {
    const theme = props.theme;
    return css `
  .component-item-detail{
    background: ${theme.ref.palette.neutral[400]};
    overflow: auto;
    padding: 0 16px 16px 16px;
    border-left: 1px solid ${theme.ref.palette.neutral[700]};
    border-right: 1px solid ${theme.ref.palette.neutral[700]};
    .ml-12{
      margin-left: ${polished.rem(12)};
    }
    .container-fluid{
      padding: 0 !important;
    }
    z-index: 3;
    font-size: ${polished.rem(14)};
    .small-text{
      font-size: ${polished.rem(13)};
    }
    .html-container{
      *{
        font-size: ${polished.rem(14)} !important;
        font-weight: normal !important;
        font-family: inherit !important;
        background: ${theme.ref.palette.neutral[400]} !important;
      }
      *:not(a):not(a *){
        color: ${theme.ref.palette.black} !important;
      }
    }
    .item-header{
      height: 41px;
      border-bottom: 1px solid ${theme.ref.palette.neutral[700]};
      height: ${polished.rem(45)};
      margin-left: -16px;
      margin-right: -16px;
      .item-header-title{
        font-size: 16px;
        line-height: 41px;
      }
    }
    .item-close{
      cursor: pointer;
      margin-right: -4px;
    }
    .item-img{
      img{
        width: 100%;
      }
    }
    .item-line{
      width: 100%;
      height: 1px;
      border-bottom: 1px solid ${theme.ref.palette.neutral[700]};
    }
    .item-detail-title, .item-detail-title:hover, .item-detail-title:focus, .item-detail-title:active{
      text-decoration: underline;
      outline: none;
      box-shadow: 0 0 0 !important;
      border: 0 !important;
      font-size: 16px;
      white-space: normal !important;
      color: ${theme.ref.palette.black};
      .item-detail-title-text{
        max-width: 290px;
      }
    }

    .ds-thumbnail{
      width:  ${polished.rem(18)};
      height:  ${polished.rem(18)};
      background-color: ${theme.sys.color.info.main};
    }

    .ds-type{
      font-size: ${polished.rem(13)};
      color: ${theme.ref.palette.neutral[1100]};
      max-width: ${polished.rem(168)};
    }
  }

  `;
}
