import { css, polished } from 'jimu-core';
export function dataBadgeStyles(props) {
    const theme = props.theme;
    return css `
    .badge{
      width: ${polished.rem(18)};
      height: ${polished.rem(18)};
      border-radius: ${polished.rem(9)};
      display: flex;
      align-items: center;
      justify-content: center;
      margin-left: 2px;
      svg{
        color: ${theme.ref.palette.black};
      }
    }

    .auth{
      background-color: ${theme.sys.color.success.dark};
    }

    .living-atlas, .premium, .subscriber{
      background-color: ${theme.sys.color.info.main};
    }

  `;
}
