import { css, polished } from 'jimu-core';
export function itemListStyles(props) {
    const theme = props.theme;
    return css `
    .modal-dialog{
      max-width: auto;
    }

    .two-line-truncate{
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      word-break: break-all;
      word-wrap: break-word;
    }

    .loading-container{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
    }

    .empty-placeholder{
      padding: ${polished.rem(16)};
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      .empty-placeholder-text, .empty-placeholder-icon{
        display: table;
        margin: 0 auto;
      }
      .empty-placeholder-text{
        color: ${theme.ref.palette.neutral[900]};
        font-size: ${polished.rem(14)};
        margin-top: 16px;
        text-align: center;
      }
      .empty-placeholder-icon{
        color: ${theme.sys.color.secondary.dark};
      }
    }

    .mt13{
      margin-top: ${polished.rem(13)};
    }

    .mt10{
      margin-top: ${polished.rem(10)};
    }

    .ds-thumbnail{
      width:  ${polished.rem(18)};
      height:  ${polished.rem(18)};
      background-color: #0095DB;
      .ds-thumbnail-icon {
        color: #FFFFFF;
      }
    }

    .ds-type{
      font-size: ${polished.rem(13)};
      color: ${theme.ref.palette.neutral[1100]};
      max-width: ${polished.rem(168)};
    }

    .component-item-list{
      width: 100%;
      height: 100%;
      padding: 0;
      position: relative;

      .detail-reference {
        width: 0;
        height: 100%;
        display: inline;
      }

      .list-container{
        overflow-x: hidden;
        overflow-y: auto;
        padding-top: 0.25rem;
      }

      .item-card-container{
        &:hover{
          border-color: transparent !important;
        }
      }

      .item-card-container.item-border-transparent{
        border: ${polished.rem(1)} solid transparent;
      }

      .item-badge-container{
        position: absolute;
        top: ${polished.rem(8)};
        left: ${polished.rem(8)};
      }

      .item-active-icon, .item-part-active-icon{
        position: absolute;
        top: -2px;
        right: -2px;

        .item-active-icon-container, .item-part-active-icon-container{
          width: ${polished.rem(26)};
          height: ${polished.rem(26)};
          line-height: ${polished.rem(26)};
          color: ${theme.ref.palette.white};
          background-color: ${theme.sys.color.primary.light};
        }

        .item-part-active-icon-container{
          padding: ${polished.rem(3)};
        }
      }

      .item-part-active-icon-container{
        .item-part-active-icon-inner-box{
          width: ${polished.rem(18)};
          height: ${polished.rem(18)};
          background-color: ${theme.ref.palette.white};
          >div{
            width: ${polished.rem(12)};
            height: ${polished.rem(12)};
            border-radius: ${polished.rem(1)};
            background-color: ${theme.sys.color.primary.light};
          }
        }
      }

      &.all-mode{
        @media only screen and (max-width: 1210px){
          .item-list{
            width: 540px;
          }
        }

        @media only screen and (min-width: 1210px){
          .item-list{
            width: 810px;
          }
        }

        @media only screen and (min-width: 1480px){
          .item-list{
            width: 1080px;
          }
        }

        @media only screen and (min-width: 1750px){
          .item-list{
            width: 1350px;
          }
        }

        @media only screen and (min-width: 2020px){
          .item-list{
            width: 1620px;
          }
        }

        @media only screen and (min-width: 2290px){
          .item-list{
            width: 1890px;
          }
        }

        @media only screen and (min-width: 2560px){
          .item-list{
              width: 2160px;
          }
        }

        @media only screen and (min-width: 2830px){
          .item-list{
              width: 2430px;
          }
        }

        @media only screen and (min-width: 3100px){
          .item-list{
              width: 2700px;
          }
        }

        .item-list{
          display: flex;
          flex-wrap: wrap;
          margin-left: -0.5rem;
          margin-right: -0.5rem;
          overflow: visible;
          min-width: ${polished.rem(540)};

          .item{
            width: ${polished.rem(240)};
            margin: 0 ${polished.rem(15)} ${polished.rem(30)} ${polished.rem(15)};

            .item-border-color-transparent{
              border: 2px solid transparent;
              .item-card{
                border: 0;
              }
              &:hover{
                border-color: ${theme.sys.color.primary.light};
                .item-card{
                  border-color: transparent !important;
                }
              }
            }
            .item-border-color-selected{
              border: 2px solid ${theme.sys.color.primary.light};
              .item-card{
                border-color: transparent !important;
              }
            }
            .item-border-color-disabled{
              border: 2px solid ${theme.ref.palette.neutral[1000]};
              &:hover{
                border-color: ${theme.ref.palette.neutral[1000]};
              }
            }
            .item-background-color-disabled{
              background-color: ${theme.ref.palette.neutral[1000]};
            }

            .item-card-container{
              padding-bottom: 122%;
              position: relative;
              overflow: hidden;

              .item-card{
                position: absolute;
                background-color: ${theme.sys.color.secondary.main};

                .item-info-container{
                  padding: ${polished.rem(12)};
                }

                .item-info-icon{
                  cursor: pointer;
                  margin-right: -4px;
                }

                .item-sub-label{
                  width: calc(100% - 30px);
                  font-size: ${polished.rem(13)};
                  color: ${theme.ref.palette.neutral[1000]};
                }

                .item-label{
                  font-size: ${polished.rem(16)};
                  line-height: ${polished.rem(20)};
                  color: ${theme.ref.palette.neutral[1100]};
                  height: ${polished.rem(40)};
                }

                .item-thumbnail{
                  background-size: 100% 100%;
                  background-position: top center;
                  cursor: pointer;
                  height: 56%;
                }
              }
            }
          }
        }
      }

      &.simple-mode{
        .item-list, .item{
          width: 100%;
        }

        .item-border-color-transparent{
          border: 1px solid transparent;
          .item-card{
            border: 0;
          }
          &:hover{
            border-color: ${theme.sys.color.primary.light};
            .item-card{
              border-color: transparent !important;
            }
          }
        }
        .item-border-color-selected{
          border: 1px solid ${theme.sys.color.primary.light};
          .item-card{
            border-color: transparent !important;
          }
        }
        .item-border-color-disabled{
          border-color: ${theme.ref.palette.neutral[1000]};
          &:hover{
            border-color: ${theme.ref.palette.neutral[1000]};
          }
        }

        .component-item-detail{
          border: 0 !important;
        }
        .item-detail-page-container{
          height: 100%;
          width: 100%;
          position: absolute;
          top: 0;
          background-color: ${theme.ref.palette.neutral[400]};
          .item-detail-container {
            height: calc(100% - 100px);
          }
          .add-btn {
            background-color: ${theme.ref.palette.neutral[500]};
            box-shadow: 0px -4px 4px rgba(0, 0, 0, 0.25);
            height: 64px;
            position: absolute;
            bottom: 0;
          }
        }
        .item-card{
          position: relative;
          background-color: ${theme.sys.color.secondary.main};
          padding: 0;
          .item-line-1{
            padding: 7px;
          }
          .item-line-2 {
            padding: 3px 7px;
            border-top: 1px solid ${theme.ref.palette.neutral[600]};
            .item-line-2-1 {
              width: calc(100% - 30px)
            }
          }
          .item-badge-container{
            position: absolute;
            top: ${polished.rem(8)};
            left: ${polished.rem(8)};
          }
          .item-active-icon{
            position: absolute;
            top: -1px;
            right: -1px;
            .item-active-icon-container{
              width: 0;
              height: 0;
              border-top: ${polished.rem(12)} ${theme.sys.color.primary.light} solid;
              border-left: ${polished.rem(12)} transparent solid;
              background-color: transparent;
            }
            .item-active-icon-container.item-active-icon-disabled{
              border-top-color: ${theme.ref.palette.neutral[1000]};
            }
          }
          .item-thumbnail{
            width: 40px;
            height: 40px;
            background-size: cover;
            background-position: center;
          }
          .item-label{
            width: calc(100% - 40px);
            font-size: ${polished.rem(14)};
            color: ${theme.ref.palette.neutral[1100]};
          }
          .item-sub-label{
            width: calc(100% - 20px);
            font-size: ${polished.rem(13)};
            color: ${theme.ref.palette.neutral[1000]};
          }
        }
      }

    }

  `;
}
